<template>
<div>


    <div style="margin:22px 30px; ">
        <el-steps :space="200" :active="3" finish-status="success">
            <el-step title="下单"></el-step>
            <el-step title="付款"></el-step>
            <el-step title="派送"></el-step>
            <el-step title="收货评价"></el-step>
        </el-steps>
    </div>
    <div class="shaddr-wrap">
        <div class="logo"><i class="fa fa-location-arrow"></i></div>
        <div v-if="receaddress!=null" class="bd">

            <div class="contact">
                {{receaddress.mobile}}
                {{receaddress.shr}}
            </div>
            <div class="addinfo">{{receaddress.addinfo}}
                <span>{{receaddress.postcode}}</span>
            </div>

        </div>

    </div>

    <table v-for="od in order.orderDetail" class="smart-table" border="1">
        <tr>
            <td class="tlabel">订单编号</td>
            <td>{{order.ddno}}</td>
            <td class="tlabel">下单时间:</td>
            <td>
                {{order.createtime}}

            </td>
        </tr>

        <tr>
            <td class="tlabel">总金额</td>
            <td >{{order.totalfee}}¥</td>

            <td class="tlabel">下单人</td>
            <td >{{order.puraccount}}({{order.purname}})</td>

        </tr>
        <tr>
            <td class="tlabel">商品</td>
            <td colspan="3">
                <img width="80" height="80" :src="hostHead+od.images[0]"/>
                {{od.spname}}
                {{od.price}}¥X{{od.count}}
            </td>
        </tr>


        <tr>
            <td class="tlabel">物流单号</td>
            <td colspan="3">
                <div style="width:300px; " v-if="od.state==8">
                    <el-input
                        placeholder="请输入物流单号"
                        v-model="wlno"
                        clearable>
                    </el-input>
                </div>
                <div v-if="od.state==3||od.state==4">{{od.wlno}}</div>

            </td>
        </tr>
        <tr v-if="od.state==8">

            <td colspan="4">
                <el-button  @click="fauoHandler" type="primary"><i class="el-icon-arrow-right"></i>送餐</el-button>
            </td>
        </tr>

    </table>

</div>
</template>

<script>
    export default {
        name: "fahuo",
        data(){
            return {
                oiid: this.$route.query.oiid,
                wlno: '',
                wltype: '顺丰',
                hostHead:this.$app.contextPath,
                order: {},
                receaddress: null
            }
        },

        props:{
            params:{
                type:Object,
                default:{}
            },
        },
        methods:{


            async getOrderInfo(){

                let {data:res}= await this.$http.post("admin/shorder/load",{id:this.params.id});
                if (res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }

                res.data.orderDetail = res.data.orderDetail.filter(c => {
                    return c.id == this.params.oiid;
                });
                console.log("订单数据",res);
                this.order=res.data;

            },
            async getAddress(){

                let {data:res}=await this.$http.post("admin/receaddress/load",{id:this.order.addid});

                if (res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                this.receaddress=res.data;
                console.log("receaddress",this.receaddress);

            },
            async fauoHandler() {
                if (this.wlno == "") {
                    this.$message.error("请输入物流号");
                    return;
                }

                let {data: res} = await this.$http.post("admin/shorder/deliver", {
                    oiid: this.params.oiid,
                    wlno: this.wlno,
                    wltype: this.wltype
                });

                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.$message.success("派送完成");
                this.$emit("close",false);

            }

        },
        async created(){
            await this.getOrderInfo();
            await this.getAddress();
        }
    }
</script>

<style scoped>

</style>
